
import React, { Component } from 'react';
import { GOOGLE_AUTH_URL, FACEBOOK_AUTH_URL, GITHUB_AUTH_URL } from '../constants/index';
import { FaGoogle, FaFacebookF, FaGithub } from 'react-icons/fa';
import { Button, Tooltip } from 'antd';

class SocialLogin extends Component {
    constructor(props) {
        super(props);
        this.state = {
            socialAccounts: [
                { background: '#dc4e41', icon: '<FaGoogle/>', name: 'Google'},
                { background: '#3b5998', icon: '<FaFacebookF/>', name: 'Facebook'},
                { background: '#00405d', icon: '<FaGithub/>', name: 'Github'},
            ]
        };
    }
    render() {
        return (
            <div className="social-login">
                    <Tooltip title={`${this.props.title} Google`}>
                        <Button shape="circle" className="ml1" style={{ 'background': "#dc4e41", 'color': '#fff' }} 
                                icon={<FaGoogle  />}
                            onClick={() => window.location.href = GOOGLE_AUTH_URL}>
                        </Button>
                    </Tooltip>
                    <Tooltip title={`${this.props.title} Facebook`}>
                        <Button shape="circle" className="ml1" style={{ 'background': "#3b5998", 'color': '#fff' }} 
                                icon={<FaFacebookF />}
                            onClick={() => window.location.href = FACEBOOK_AUTH_URL}>
                        </Button>
                    </Tooltip>
                    <Tooltip title={`${this.props.title} GitHub`}>
                        <Button shape="circle" className="ml1" style={{ 'background': "#00405d", 'color': '#fff' }} 
                                icon={<FaGithub />}
                            onClick={() => window.location.href = GITHUB_AUTH_URL}>
                        </Button>
                    </Tooltip>
            </div>
        );
    }
}
export default SocialLogin